@import url(https://fonts.googleapis.com/css?family=Fira+Sans:300,400,500&display=swap);
nav ul,
footer ul{
    list-style: none;
}

nav a,
footer a {
    text-decoration: none;
    color: inherit;
    font-family: var(--fira);
    font-weight: 400;
}

@media only screen and (min-device-width: 850px){
    /* Position (flex) properties for nav and footer */
    nav,
    footer {
        display: flex;
        padding: 1.875rem;
        align-items: center;
    }

    nav {
        justify-content: flex-end;
    }

    nav .brand {
        margin-right: auto;
    }

    footer {
        justify-content: flex-start;
    }

    footer .footerSocial {
        margin-left: auto;
    }
    
    footer li{
        display: inline-block;
        padding: 0 0.625rem;
    }

    /* Design for both navbar elements */

    nav .navLinks li{
        display: inline-block;
        padding: 1rem 0.75rem;
        margin: 0 0.25rem;
        -webkit-transition: background-color 0.3s ease;
        transition: background-color 0.3s ease;
    }

    nav .navLinks li:hover{
        background-color: rgba(0, 0, 0, 0.03);
    }

    #register {
        background-color: var(--brand-color);
        color: white;
        padding: 1rem 0.85rem;
    }

    #register:hover {
        background-color: #123566;
    }
}

@media only screen and (max-device-width: 849px){
    nav {
        display: flex;
        justify-content: flex-start;
        padding: 1.875rem;
    }

    nav .brand{
        margin: 0 auto;
        order: 10;
    }

    nav .navLinks li{
        display: none;
    }


    footer {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    footer .footerLinks {
        display: none;
    }
}
.home {font-family: var(--fira)}
.home .heading {font-weight: 500; font-size: 2.7rem;}
.home .subheading {font-weight: 500}
.description {font-weight: 300}

.jumbotron {
    background-color: var(--brand-color);
    color: white;
}

.about {background-color: var(--secondary-color); height: auto;}

@media only screen and (min-device-width: 850px){
    /* Position (flex) properties for jumbotron */
    .jumbotron {height: 50vh}
    .jumbotron .container {
        height: inherit;
        display: grid;
        grid-template-columns: (2, 1fr);
    }

    .container .jumboImage {
        grid-column-start: 1;
        grid-column-end: 2;
    }

    .container .jumboText {
        grid-column-start: 2;
        grid-column-end: 3;
        display: flex;
        flex-flow: column;
        align-items: center;
        justify-content: center;
    }

    /* Design for jumbotron elements */
    .jumbotron .container .heading {
        border: solid 0.35rem white;
        padding: 0.6rem 0.6rem;
        text-align: center;
    }

    .jumbotron h3 {
        margin: 1rem;
    }

    /* Position (flex) properties for currentPrograms */
    .currentPrograms .programs {
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
    }
    /* Design for currentPrograms' elements */
    .currentPrograms .container .heading{
        margin-top: 2rem;
        margin-bottom: 1.5rem;
        text-align: center;
    }

    .p-item {
        border: solid 0.02rem black;
        margin: 1.5rem;
        padding: 1rem;
        flex-basis: 25%;
    }

    /* Position (flex) properties for about */
    .about .content {
        display: flex;
        justify-content: space-between;
    }

    .about .image {
        margin: 0 auto;
    }
    /* Design for about elements */
    .about .description {
        font-weight: 300;
    }

    .about .container .heading {
        padding-top: 5rem;
    }
}
:root {
    --fira: 'Fira Sans', sans-serif;
    --brand-color: #184586;
    --secondary-color: #F9FAFA;
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.container {
    width: 92%;
    margin: 0 auto;
}

