nav ul,
footer ul{
    list-style: none;
}

nav a,
footer a {
    text-decoration: none;
    color: inherit;
    font-family: var(--fira);
    font-weight: 400;
}

@media only screen and (min-device-width: 850px){
    /* Position (flex) properties for nav and footer */
    nav,
    footer {
        display: flex;
        padding: 1.875rem;
        align-items: center;
    }

    nav {
        justify-content: flex-end;
    }

    nav .brand {
        margin-right: auto;
    }

    footer {
        justify-content: flex-start;
    }

    footer .footerSocial {
        margin-left: auto;
    }
    
    footer li{
        display: inline-block;
        padding: 0 0.625rem;
    }

    /* Design for both navbar elements */

    nav .navLinks li{
        display: inline-block;
        padding: 1rem 0.75rem;
        margin: 0 0.25rem;
        transition: background-color 0.3s ease;
    }

    nav .navLinks li:hover{
        background-color: rgba(0, 0, 0, 0.03);
    }

    #register {
        background-color: var(--brand-color);
        color: white;
        padding: 1rem 0.85rem;
    }

    #register:hover {
        background-color: #123566;
    }
}

@media only screen and (max-device-width: 849px){
    nav {
        display: flex;
        justify-content: flex-start;
        padding: 1.875rem;
    }

    nav .brand{
        margin: 0 auto;
        order: 10;
    }

    nav .navLinks li{
        display: none;
    }


    footer {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    footer .footerLinks {
        display: none;
    }
}